@import url('https://fonts.googleapis.com/css2?family=Dela+Gothic+One&family=Viga&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Dela+Gothic+One&family=Rowdies:wght@300&display=swap');


body {
   /* Set height to 100% of the viewport height */
  margin: 0;
  padding: 0;
  overflow-y: scroll; /* Enable vertical scroll */
}


.container {
  width: 100vw;
  min-height: 100vh; /* Adjust the desired minimum height */
  overflow: hidden;
}

.mypic1{
    width: 620px;
    position: absolute;
    left: 780px; /* Adjust the left value as needed */
    top: -110px; /* Adjust the top value as needed */
    z-index: -1;
  
}



.emoji1 {
  position: absolute;
  top: 210px;
  left: 420px;
  height: 70px;
  animation: bounce 3s infinite;
  transform-origin: bottom;
}

@keyframes bounce {
  0% {
    transform: translateY(0);
  }
  25% {
    transform: translateY(-5px);
  }
  50% {
    transform: translateY(0);
  }
  75% {
    transform: translateY(-5px);
  }
  100% {
    transform: translateY(0);
  }
}

.circle {
  width: 0px;
  height: 0px;
  border-radius: 50%;
  position: relative;
  background-color: #FF8080;
  box-shadow: 0 0 400px 100px rgba(255, 26, 26, 0.5);
  filter: blur(5px);
  opacity: 0.9;
  left: 1200px;
  top: -00px;
  z-index: -2;
  animation: circleColorChange 30s infinite;
}

.circle2 {
  width: 0px;
  height: 0px;
  border-radius: 50%;
  position: relative;
  background-color: #3e62f4;
  box-shadow: 0 0 200px 100px rgba(99, 87, 233, 0.5);
  filter: blur(5px);
  opacity: 0.9;
  left: 1000px;
  bottom: -100px;
  animation: circle2ColorChange 30s infinite;
  z-index: -2;
}

@keyframes circleColorChange {
  0%, 50%, 100% {
    background-color: #FF8080;
    box-shadow: 0 0 400px 200px rgba(255, 26, 26, 0.5);
  }
  25%, 75% {
    background-color: #FF8080;
    box-shadow: 0 0 400px 300px rgba(99, 87, 233, 0.5);
  }
}

@keyframes circle2ColorChange {
  0%, 50%, 100% {
    background-color: #3e62f4;
    box-shadow: 0 0 400px 300px rgba(99, 87, 233, 0.5);
  }
  25%, 75% {
    background-color: #3e62f4;
    box-shadow: 0 0 400px 200px rgba(255, 26, 26, 0.5);
  }
}




