@import url('https://fonts.googleapis.com/css2?family=Agdasima&family=Ysabeau+SC:wght@300;400&display=swap');

.logo1 {
  width: 150px;
  height: 150px;
  position: absolute;
  left: -15px;
  top: -40px;
  z-index: 999;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  backdrop-filter: blur(8px);
  z-index: 999;
  position: relative;
}

.logo-container {
  display: flex;
  align-items: center;
}

.menu-icon {
  display: flex;
  align-items: center;
  cursor: pointer;
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 999;
}

.menu-icon-open {
  color: #333;
  font-size: 24px;
}

.menu-icon-close {
  color: #333;
  font-size: 24px;
}

.close-icon {
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
  color: #333;
  font-size: 24px;
}

.nav-links {
  list-style: none;
  display: flex;
  position: relative;
  z-index: 9999;
}

.nav-links li {
  margin-right: 20px;
  padding-left: 40px;
}

.nav-links a {
  text-decoration: none;
  color: #333;
  font-weight: bold;
  z-index: 3;
  position: relative;
  font-family: 'Ysabeau SC', sans-serif;
}

.nav-links a::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: -2px;
  left: 0;
  background-color: transparent;
  transition: background-color 0.3s ease;
}

.nav-links a:hover::before {
  background-color: #ffbd59;
}

.nav-links a:hover {
  color: #ffbd59;
}
.menu-icon{
  display: none;
}

@media (max-width: 600px) {
  .logo-container {
    justify-content: flex-start;
  }

  .menu-icon {
    display: flex;
    align-items: center;
    cursor: pointer;
    position: absolute;
    top: 20px;
    right: 30px;
    z-index: 999;
  }

  .menu-icon-bar {
    width: 25px;
    height: 3px;
    background-color: #333;
    margin: 6px 0;
    transition: background-color 0.3s ease;
  }

  .menu-icon.open .menu-icon-bar:nth-child(1) {
    transform: rotate(-45deg) translate(-5px, 6px);
  }

  .menu-icon.open .menu-icon-bar:nth-child(2) {
    opacity: 0;
  }

  .menu-icon.open .menu-icon-bar:nth-child(3) {
    transform: rotate(45deg) translate(-5px, -6px);
  }

 

  .close-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 20px;
    right: 20px;
    cursor: pointer;
    color: #333;
    font-size: 24px;
    z-index: 999;
  }

  .nav-links {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    flex-direction: column;
    background-color: #fff;
    padding: 40px;
    text-align: center;
    z-index: 9999;
  }

  .nav-links.open {
    display: flex;
    align-items: center;
  }

  .nav-links li {
    z-index: 9999;
    margin-right: 0;
    padding-left: 0;
    margin-bottom: 20px;
  }
}


