/* Existing CSS code */
html, body {
  margin: 0;
  padding: 0;
}
.maincontainer{
  position: relative;
  margin-top: 200px;
}
.card-container1 {
  position: absolute;
  display: flex;
  background-color: transparent;
  padding: 0px;
  backdrop-filter: blur(8px);
  -webkit-backdrop-filter: blur(8px); /* Add this line for Safari support */
  margin-bottom: 200px;
  transition: transform 0.3s ease;
}

.card1 {
  width: 245px;
  height: 320px;
  margin-top: -200px;
  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 8px;
  margin-right: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 20px;
  transition: transform 0.3s ease;
}

.card1:hover {
  transform: scale(1.05); /* Increase the scale on hover */
}

.card-icon1 {
  width: 200px;
  height: 200px;
  margin-bottom: 10px;
}

.card-title1 {
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 10px;
}

.card-description1 {
  font-size: 14px;
  text-align: justify;
}

.heading3 {
  position: absolute;
  z-index: 999;
  padding-bottom: -60px;
  margin-left: 500px;
  margin-top: -360px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
}

.circle7 {
  margin-top: 300px;
  width: 2500px;
  z-index: -1;
  border-radius: 100%;
  position: absolute;
  background-color: #ff8080;
  box-shadow: 0 0 100px 500px rgba(255, 26, 26, 0.5);
  filter: blur(5px);
  opacity: 0.7;
  top: 50%;
  left: 50px;
  z-index: -1;
  transform: translate(-50%, -50%);
  animation: circleColorChange7 10s infinite;
}

@keyframes circleColorChange7 {
  0% {
    box-shadow: 0 0 100px 940px rgba(250, 143, 143, 0.5); /* Adjust the box shadow color */
  }
  50% {
    box-shadow: 0 0 100px 940px rgba(26, 126, 248, 0.5); /* Adjust the box shadow color */
  }
  100% {
    box-shadow: 0 0 100px 940px rgba(227, 128, 255, 0.5); /* Adjust the box shadow color */
  }
}



/* Media query for phone devices */
@media (max-width: 600px) {
  .card-container1 {
    position: relative;
    top: -450px;
    flex-direction: column;
    align-items: center;
    z-index: 999;
  }

  .card1 {
    width: 90%;
    margin-top: 20px;
    margin-right: 0;
  }

  .heading3 {
    position: relative;
    top: -450px;
    margin-left: -5px;
    margin-bottom: 30px;
    text-align: center;
  }




  /* Animation for circle7 on phone devices */
  @keyframes circleColorChange7-phone {
    0% {
      box-shadow: 0 0 100px 1040px rgba(250, 143, 143, 0.5);
    }
    50% {
      box-shadow: 0 0 100px 1040px rgba(26, 126, 248, 0.5);
    }
    100% {
      box-shadow: 0 0 100px 1040px rgba(227, 128, 255, 0.5);
    }
  }

  .circle7 {
    position: relative;
    top: 1700px;
    display: block;
    z-index: -1;
    animation: circleColorChange7-phone 10s infinite;
  }
}

