/* Existing CSS code */

body {
  overflow-y: scroll;
  overflow-x: hidden;
}

.note {
  color: #ffb835;
  font-size: 100px;
  margin-left: 300px;
  margin-top: 200px;
  width: 1500px;
}

/* Media query for smaller screen sizes */
@media (max-width: 600px) {
  .note {
    position: absolute;
    margin-left: 50px;
    margin-top: 1580px;
    z-index: 999;
    width: 600px;
    font-size: 30px;
  }

  .effectpro {
    width: 200px;
    z-index: -1;
    margin-top: -350px;
    height: 0px;
    border-radius: 50%;
    position: relative;
    background-color: #ff8080;
    box-shadow: 0 0 100px 500px rgba(255, 26, 26, 0.5);
    filter: blur(5px);
    opacity: 0.7;
    top: 50%;
    left: 50px;
    transform: translate(-50%, -50%);
    animation: circleColorChange 10s infinite;
  }
  
  @keyframes circleColorChange {
    0% {
      box-shadow: 0 0 100px 340px rgba(250, 143, 143, 0.5);
    }
    50% {
      box-shadow: 0 0 100px 340px rgba(26, 126, 248, 0.5);
    }
    100% {
      box-shadow: 0 0 100px 340px rgba(227, 128, 255, 0.5);
    }
  }
  
}
