body{
  overflow-y: scroll;
  overflow-x: hidden; /* Hide horizontal overflow */
}
.aboutcontainer{
  position: relative;
  margin-top: -350px;
}

.profile1 {
  width: 660px;
  position: relative;
  top: -200px;
  right: -10px;
  z-index: 1;
  filter: drop-shadow(0 2px 6px rgba(0, 0, 0, 0.2));
}

.circleeffect {
  width: 1500px;
  height: 0px;
  margin-top:-90px ;
  border-radius: 0%;
  position: relative;
  background-color: hsl(46, 99%, 55%);
  box-shadow: 0 0 200px 400px rgba(19, 111, 222, 0.5);
  opacity: 0.9;
  left: 200px;
  top: -500px;
  z-index: 0; /* Adjusted the z-index */
  animation: circleColorChange8 10s infinite;
}

@keyframes circleColorChange8 {
  0% {
    
    box-shadow: 0 0 100px 440px rgba(250, 143, 143, 0.5); /* Adjust the box shadow color */
  }
  50% {
 
    box-shadow: 0 0 100px 440px rgba(26, 126, 248, 0.5); /* Adjust the box shadow color */
  }
  100% {
    box-shadow: 0 0 100px 440px rgba(227, 128, 255, 0.5); /* Adjust the box shadow color */
  }
}


.heading {
  color: #fbbb5c;
  position: absolute;
  font-size: 150px;
  margin-top: -300px;
  margin-left: 570px;
  width: 600px;
  z-index: 999;
  filter: none; /* Remove the blur filter */
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3); /* Add text shadow */
}


.about-text {
  position: absolute;
  margin-left: 550px;
  margin-top: -100px;
  width: 700px; /* Adjust the width as needed */
  padding-left: 20px;
  padding-right: 20px;
  text-align: justify;
  text-justify: inter-word;
}

.bullet {
  font-weight: bold;
  font-size: large;
  padding-bottom: 20px;
}

.thanks {
  padding-top: 15px;
}

.emoji {
  position: absolute;
  width: 150px;
  margin-left: 1200px;
  margin-top: 30px;
  z-index: 1;
  animation: jumpAnimation 1s alternate infinite;
}

@keyframes jumpAnimation {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-20px);
  }
  100% {
    transform: translateY(0);
  }
}


.social-icons {
  padding: 20px;
  margin-left: 600px;
  position: relative;
  bottom: -300px;
  width: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  margin-top: 20px;
  z-index: 999; /* Adjusted the z-index */
}

.social-icons a {
  color: #333;
  font-size: 24px;
}

.social-icons a:hover{
  color: #ffb341;
}

@media (max-width: 600px) {
  .profile1 {
    position: relative;
    width: 100%; /* Adjust the width as needed */
    top: 1800px;
    right: 0;
    margin: 0 auto;
  }
  
  .circleeffect {
    position: absolute;
    width: 100%;
    top: 400px; /* Adjust the positioning as needed */
    left: 0;
  }
  .circleeffect1 {
    position: absolute;
    width: 100%;
    height: 500%;
    margin-top: 2900px;
    left: 0;
    background-color: hsl(46, 99%, 55%);
    box-shadow: 0 0 200px 700px rgba(19, 111, 222, 0.5);
    opacity: 0.3;
    z-index: -1;
    animation: boxShadowChange1 5s infinite;
  }
  
  .heading {
    position: absolute;
    font-size: 80px; /* Adjust the font size as needed */
    margin-top: 1450px;
    margin-left: 10%;
    width: 80%; /* Adjust the width as needed */
  }
  
  .about-text {
    position: absolute;
    margin-left: 10%;
    margin-top: 2030px; /* Adjust the positioning as needed */
    width: 80%; /* Adjust the width as needed */
  }
  
  .emoji {
    width: 100px; /* Adjust the width as needed */
    margin-left: 70%; /* Adjust the positioning as needed */
    margin-top: 1757px;
  }
  
  .social-icons {
    display: none;
    position: absolute;
    z-index: 9999;
    margin-left: -60px;
    bottom: -4100px; /* Adjust the positioning as needed */
  }
}

